import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'

import http from "@/http";

export default {
directives: {
TransferDom
},
components: {
Cell, XInput, Group, XButton, Flexbox, FlexboxItem, Grid, GridItem, XHeader, XDialog, Qrcode
},
methods: {
getUserInfo(){
http('user-center-server/user/current', {})
.then(function(suc){
if(suc){
this.user = suc;
this.qrcodeUrl = window.location.origin + '/Login/Index/' + suc.accountId;
}
}.bind(this),function (err) {});
},
/*获取订单数*/
getOrderCount(){
http('ht-mj-order-server/order/wx/myOrderCount', {})
.then(function(suc){
if(suc){
this.orderCount = suc;
}
}.bind(this), function (err) {});
},
/*获取预存款*/
getDeposit(){
http('ht-mj-account-server/portalpredeposit', {})
.then(function(suc){
if(suc && suc.balance){
this.deposit = suc.balance;
}
}.bind(this), function (err) {});
}
},
mounted: function () {
let token = localStorage['x-security-token'];
if(token){
this.getOrderCount();
this.getUserInfo();
this.getDeposit();
}else{
this.$router.replace('/Login?redirect_uri=' + this.redirect_uri);
}
},
data () {
return {
minHeight:document.documentElement.clientHeight,
redirect_uri: encodeURIComponent(location.pathname),
showHideOnBlur: false,
qrcodeUrl:window.location.origin + '/Login',
user:{},
orderCount:0,
deposit:0
}
}
}



